.slide_1{
  min-height: 90vh;
  display: flex !important;
  align-items: center;
  justify-content: center;
  padding: 50px 15px;
  background: url(./assets/banner_slider.jpg) center no-repeat;
  background-size: cover;
}
.banner_slider .slick-prev{
  left: 20px;
  z-index: 9;
}
.banner_slider .slick-next{
  right: 20px;
}
.banner_slider .slick-next:before,.banner_slider .slick-prev:before{
  display: none;
}
.banner_slider .slick-prev, .banner_slider .slick-next,.banner_slider .slick-prev:hover, .banner_slider .slick-next:hover {
  color: #4aab31;
  font-size: 16px;
  border: 2px solid;
  border-radius: 50px;
  line-height: 26px;
  text-align: center;
  width: 30px;
  height: 30px;
}
.banner_slider .slick-dots {
  bottom: 10px;
}
.banner_slider .slick-dots li button:before {
  content: '';
  width: 12px;
  height: 12px;
  border-radius: 8px;
  border: 2px solid #4aab31;
  opacity: 1 !important;
}
.banner_slider .slick-dots li.slick-active button:before {
  background: #4aab31;
}
.banner_btn {
