.custom_checkbox{
  display: inline-block;
  margin-right: 10px;
  cursor: pointer;
}
.custom_checkbox *{
  cursor: pointer;
}
.custom_checkbox .check_box {
  display: inline-block;
}
.custom_checkbox .check_box:before {
  content: "";
  width: 10px;
  height: 10px;
  border: 1px solid #fff;
  display: inline-block;
  border-radius: 50%;
}
.custom_checkbox input{
  display: none;
}
.custom_checkbox input:checked ~ .check_box:before {
  background-color: #fff;
}
