/** media queries **/
@media (min-width: 768px){
  .navbar-expand-md {
      justify-content: space-between;
      padding: 0px;
  }
  .navbar-collapse {
      flex-grow: initial;
  }
  .navbar-expand-md .navbar-nav .dark_item .nav-link{
      padding: 10px 30px;
  }
  .navbar-expand-md .navbar-nav .b_l_1 .nav-link{
      border-left:1px solid #eee;
      padding: 10px 30px;
  }
  .product_row .col-md-4{
      padding-left: 7px;
      padding-right: 7px;
  }
  .product_row .col-md-4:first-child{
      padding-left: 0px;
  }
  .product_row .col-md-4:last-child{
      padding-right: 0px;
  }
  .big_text{
      font-size: 50px;
  }
  .service_box{
      padding:30px 20px;
  }
  .sm_p_h_30{
      padding-left: 30px;
      padding-right:30px;
  }
  .slide_mr_30.wwd_slider .slick-slide{
      margin-right: 30px;
  }
}
@media (max-width:991px){
  .slick-prev, .slick-next{
      display: none !important;
  }
}
@media (max-width:767px){
  .sm_banner.front_banner {
      padding: 40px 0px;
  }
  .sm_banner.front_banner img{
      display: none;
  }
}